const EventDetailsHeader = () => {
    return (
        <h1 className='mb-3 d-flex text-uppercase fw-bold text-align-center justify-content-center' 
        style={{
            margin:'auto'
        }}>
            BESTER &nbsp;
            <p className='text-warning'> EVENTS</p>
        </h1>
    )
}


function EventsDetails() {
    const data = [
        {title: "5/3 ",
        src:ShinkanGaImg, 
        desc:"GW"
        },
        {title: "5/14 ",
        src:GogatsuImg, 
        desc:"5BESTER0"
        },
        {title: "5/21 BBQ",
        src:BBQImg, 
        desc:"5BBQBBQOBOG1"
        },
        {title: "7/31 ",
        src:HanabiImg, 
        desc:"7back number.."
        },
        {title: "8/15 ",
        src:NatsugaImg, 
        desc:"BESTERBBQ"
        },
        {title: "9/13 ",
        src:AkigaImg, 
        desc:"21BESTER"
        },
        {title: "10/31 ",
        src:ImohoriImg, 
        desc:"BESTERBESTER2..."
        },
        {title: "12/1 ",
        src:IchoImg, 
        desc:"121,21,2BESTER4"
        },
        {title: "12/15 ",
        src:ChristmasImg, 
        desc:""
        },
        {title: "2/1 ",
        src:BestercupImg, 
        desc:""
        },
        {title: "3/10 ",
        src:HarugaImg, 
        desc:"444..."
        },
    ]

    
    const EventCards = data.map((item) =>
    <motion.div
    variants={ContainerVariants()}
    initial="hidden"
    whileInView="visible"
    key={item.title}>
        <div className='container justify-content-space-between text-align-center shadow'
        style={{
        width: '80%',
        marginBottom: '10vh',
        borderRadius: '10px', // 
        backgroundColor: '#f0f0f0', // 
        padding: '20px',
        }}>
            <div className='row'>
                <div className='col-lg-6 d-flex justify-content-center' >
                    <img src={item.src} className='img-fluid border border-4 border-warning' alt="" style={{
                        height:'80%',
                        margin:'auto',
                        justifyContent:'center',

                    }}/>
                </div>
                <div className='col-lg-6 d-flex flex-column align-items-center justify-content-center mb-5 mb-lg-2'>
                    <b style={{
                        borderBottom: 'solid',
                        // borderBottomColor: 'rgba(255, 187, 0, 0.8)',
                        borderBottomWidth: '3px'
                    }}>{item.title}</b>
                    <div className='py-2 px-1'>
                    {item.desc}
                    </div>
                </div>
            </div>
        </div>
    </motion.div>
        )
    return (
        <div>
            <div className='container py-5 justify-content-center'
            style={{
                marginTop: '150px'
            }}>
                <EventDetailsHeader/>
                <div className='eventCards'
                style={{
                    marginTop: '100px'
                }}>
                    {EventCards}
                </div>
            </div>
        </div>
    )
}

export default EventsDetails
